<template>
  <div class="about">
    <h1>Scholarships</h1>
    <div v-for="(scholarship, index) in scholarships" :key="scholarship.id">
        <div>
            <div style="padding-top:13px">
                <div>
                    <h3 class="headline">{{ scholarship.name }}</h3>
                    <template v-if="scholarship.latest_detail">
                        <p>Notification Release Date - {{ scholarship.latest_detail.notification_release_date }}</p>
                        <p>Last Date - {{ scholarship.latest_detail.last_date }}</p>
                    </template>
                </div>
            </div>
            <hr>
        </div>
    </div>
  </div>
</template>

<script>
import firebase from '../configFirebase.js'

export default {
    data() {
        return {
            scholarships: []
        }
    },

    mounted() {
    firebase.db.collection('scholarships').orderBy('created_at','desc').onSnapshot((snapShot) => {
            this.scholarships=[];
            snapShot.forEach((scholarship)  => {
                // console.log(scholarship.data());
                this.scholarships.push({
                    id: scholarship.id,
                    name: scholarship.data().name,
                    latest_detail: scholarship.data().latest_detail,
                })
            });
        });
    }
}
</script>
